import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import zIndex from "@material-ui/core/styles/zIndex";
import { Grid } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  imgTop: {
    position: "relative",
    width: "100%",
    height: "80vh",
    "&:hover, &:focus": {
      zIndex:2,
    },
    maxHeight:'100%',
    float:'left',
    // transitionProperty: "all",
    // transitionDuration:'2s',
    // animation:`$zEffectLow 3000ms infinite ${theme.transitions.easing.easeInOut}`,
    transition: `all 2000ms ${theme.transitions.easing.easeInOut}`,
    transitionDuration:'0.25s',
    transitionDelay:'0.25s'
  },
  imgTopDown: {
    position: "relative",
    width: "100%",
    height: "80vh",
    "&:hover, &:focus": {
      zIndex:2,
    },
    maxHeight:'100%',
    float:'left',
    // transitionProperty: "all",
    // transitionDuration:'2s',
    // animation:`$zEffectLow 3000ms infinite ${theme.transitions.easing.easeInOut}`,
    transition: `all 2000ms ${theme.transitions.easing.easeInOut}`,
    transitionDuration:'0.25s',
    transitionDelay:'0.25s',
    filter: 'grayscale(100%)',
    opacity:'0.5'
  },
  imgLower: {
    position: "absolute",
    width: "200vw",
    height: "auto",
    left:'0vw',
    "&:hover, &:focus": {
      zIndex:'3',
      opacity:'1'
    },
    top:'20vh',
    maxHeight: "100%",
    transition: `all 2000ms ${theme.transitions.easing.easeInOut}`,
    transitionDuration:'0.25s',
    transitionDelay:'0.25s',
    zIndex:'3'
  },
  imgLowerDown: {
    position: "absolute",
    width: "200vw",
    height: "auto",
    left:'0vw',
    "&:hover, &:focus": {
      // animation:`$zEffectAbv 3000ms ${theme.transitions.easing.easeInOut}`,
      zIndex:'3',
      opacity:'1',
    },
    top:'20vh',
    maxHeight: "100%",
    filter: 'grayscale(100%)',
    transition: `all 2000ms ${theme.transitions.easing.easeInOut}`,
    transitionDuration:'0.25s',
    transitionDelay:'0.25s',
    zIndex:'-1',
    opacity:'0.5'
  },
  itemGridImg1:{
    [theme.breakpoints.between('xs', 'sm')]: {
        position:'relative',
        boxSizing:'border-box',
        flexBasis:'auto',
        flexWrap:'1',
        display:'block',
        height:'70vh',
        width:'100%',
        // border:'1px solid grey',
    },
    [theme.breakpoints.between('sm', 'md')]: {
        position:'relative',
        boxSizing:'border-box',
        flexBasis:'auto',
        flexWrap:'1',
        display:'block',
        height:'70vh',
        width:'100%',
        // border:'1px solid grey',
    },
    [theme.breakpoints.up('lg')]: {
        position:'relative',
        boxSizing:'border-box',
        flexBasis:'auto',
        flexWrap:'1',
        display:'block',
        height:'70vh',
        width:'48%',
        top:'-10%',
        border:'5px solid grey',
    },
    },
    greyGrid:{
        display:'block',
        flexDirection:'row',
        flexWrap:'wrap',
        justifyContent:'space-between',
        marginLeft:'9%',
        marginRight:'9%',
        width:'100%',
        paddingTop:'8vh',
        paddingBottom:'16vh',
        alignItems:'center',
        border:'5px solid grey',
        backgroundColor: '#F3F5F4',
    },
    greyContainer:{
        // backgroundColor: '#F3F5F4',
    },
}));

export default function Coretan() {
  const classes = useStyles(); // how to assign UseStyle
  const [hoverImgTop, setHoverImgTop] = useState(false);

  function changeClassTrue() {
    setHoverImgTop(true)
  }

          
//   useEffect(() => {
//     const interval = setInterval(() => {
//       setHoverImgTop(true)
//     }, 5000);
//     const intervalFalse = setInterval(() => {
//       setHoverImgTop(false)
//     }, 10000);
//     return () => clearInterval(interval, intervalFalse);

//   },[]);

  return (
    <div>
    <Grid container className={classes.greyContainer}>
    <Grid container className={classes.greyGrid}>
    <Grid item xl={6} lg={6} md={6} xs={12} sm={12} className={classes.itemGridImg1}>
      {hoverImgTop === true ? 
      <img
        className={classes.imgTop}
        src="data:image/jpeg;base64,"
        alt="gambar1"
        onMouseOver={() => setHoverImgTop(true)}
        onMouseLeave={()=>setHoverImgTop(true)}
      /> :
      <img
        className={classes.imgTopDown}
        src="data:image/jpeg;base64,"
        alt="gambar1"
        onMouseOver={() => setHoverImgTop(true)}
        onMouseLeave={()=>setHoverImgTop(true)}
      />}
      {hoverImgTop === true ?       
      <img
        className={classes.imgLowerDown}
        src="https://cdn.pixabay.com/photo/2015/04/23/22/00/tree-736885__480.jpg"
        alt="gambar2"
        onMouseOver={()=>setHoverImgTop(false)}
        onMouseLeave={()=>setHoverImgTop(false)}
      /> :       
      <img
        className={classes.imgLower}
        src="https://cdn.pixabay.com/photo/2015/04/23/22/00/tree-736885__480.jpg"
        alt="gambar2"
        onMouseOver={()=>setHoverImgTop(false)}
        onMouseLeave={()=>setHoverImgTop(false)}
      />}
    </Grid>
    </Grid>
    </Grid>
    {console.log(hoverImgTop)}
    </div>
  );
}

